@font-face {
  font-family: Bellefair;
  src: url("Bellefair-Regular.72707190.ttf");
}

@font-face {
  font-family: Barlow Condensed;
  src: url("BarlowCondensed-Regular.6010f9c9.ttf");
}

@font-face {
  font-family: Barlow;
  src: url("Barlow-Regular.ea60c5e0.ttf");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  color: #fff;
  text-transform: uppercase;
  font-family: Bellefair;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

h1 {
  font-size: 150px;
}

h2 {
  font-size: 100px;
}

h3 {
  font-size: 56px;
}

h4 {
  font-size: 32px;
}

h5 {
  color: #d0d6f9;
  letter-spacing: 4.725px;
  font-family: Barlow Condensed;
  font-size: 28px;
}

h6 {
  color: #fff;
  letter-spacing: 4.725px;
  font-family: Barlow Condensed;
  font-size: 28px;
}

.bg-destination {
  background: url("background-destination-desktop.f02dec08.jpg") center / cover no-repeat;
}

.bg-crew {
  background: url("background-crew-desktop.8f3d15db.jpg") center / cover no-repeat;
}

.bg-technology {
  background: url("background-technology-desktop.9baf133d.jpg") center / cover no-repeat;
}

body {
  background: url("background-home-desktop.0901cd34.jpg") center / cover no-repeat;
  flex-direction: column;
  align-items: flex-start;
  min-height: 100vh;
  display: flex;
  overflow-x: hidden;
}

.fw-700 {
  font-weight: 700;
}

header {
  justify-content: space-between;
  width: 100%;
  padding: 50px 0;
  display: flex;
}

header .cross, header .burger {
  display: none;
}

header .header__logo {
  align-self: center;
  padding: 0 50px;
}

header .line {
  opacity: .2515;
  z-index: 1;
  background: #fff;
  align-self: center;
  width: 573px;
  height: 1px;
  margin: 32px 0;
  position: relative;
  right: -25px;
}

header .header__nav {
  -webkit-backdrop-filter: blur(40.7742px);
  backdrop-filter: blur(40.7742px);
  background: #ffffff0a;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 830px;
  height: 96px;
  display: flex;
}

header .header__nav .tabs-highlighter {
  background: #fff;
  width: 72px;
  height: 3px;
  transition: all .3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 116px;
}

header .header__nav ul {
  justify-content: space-around;
  align-items: center;
  width: 80%;
  height: 100%;
  display: flex;
}

header .header__nav ul li {
  align-items: center;
  height: 100%;
  list-style: none;
  display: flex;
}

header .header__nav ul li a {
  color: #fff;
  letter-spacing: 2.7px;
  text-transform: uppercase;
  cursor: pointer;
  border: 3px solid #0000;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-family: Barlow Condensed;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
  display: flex;
}

header .header__nav ul li a:hover {
  border-bottom: 3px outset #ffffff80;
}

header .header__nav ul li a span {
  margin-right: 12px;
}

.hidden {
  display: none !important;
}

section.home {
  justify-content: space-between;
  max-width: 1080px;
  margin: 200px auto 110px;
}

section.technology .fw-700 {
  opacity: .25;
  margin-right: 10px;
}

section.technology .container__techinfo {
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  height: 700px;
  display: flex;
}

section.technology .container__techinfo .container__menu_image {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

section.technology .container__techinfo .container__menu_image .tech-menu {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 500px;
  display: flex;
}

section.technology .container__techinfo .container__menu_image .tech-menu .tech-selector {
  flex-direction: column;
  justify-content: space-between;
  width: 150px;
  height: 300px;
  display: flex;
}

section.technology .container__techinfo .container__menu_image .tech-menu .tech-selector .circle {
  cursor: pointer;
  color: #fff;
  text-align: center;
  background: none;
  border: 1px solid #ffffff40;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  margin-right: 1.5px;
  font-family: Bellefair;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: inline-flex;
}

section.technology .container__techinfo .container__menu_image .tech-menu .tech-selector .circle:hover {
  border: 1px solid #fff;
}

section.technology .container__techinfo .container__menu_image .tech-menu .tech-selector .active {
  color: #0b0d17;
  background: #fff;
}

section.technology .container__techinfo .container__menu_image .tech-content h4 {
  color: #d0d6f9;
  letter-spacing: 2.7px;
  margin-bottom: 10px;
  font-family: Barlow Condensed;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

section.technology .container__techinfo .container__menu_image .tech-content h3 {
  margin-bottom: 15px;
}

section.technology .container__techinfo .container__menu_image .tech-content p {
  color: #d0d6f9;
  width: 444px;
  font-family: Barlow;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

section.technology .container__techinfo .container__menu_image .container__tech_image {
  width: 552px;
  height: 640px;
}

section.technology .container__techinfo .container__menu_image .container__tech_image .tech-image {
  mix-blend-mode: screen;
  background: url("image-launch-vehicle-portrait.288b9867.jpg") center no-repeat;
  width: 100%;
  height: 100%;
}

section.technology .container__techinfo .container__menu_image .container__tech_image .Spaceport {
  mix-blend-mode: screen;
  background: url("image-spaceport-portrait.65662893.jpg") center no-repeat;
}

section.technology .container__techinfo .container__menu_image .container__tech_image .Space-capsule {
  mix-blend-mode: screen;
  background: url("image-space-capsule-portrait.043dc659.jpg") center no-repeat;
}

section.crew {
  justify-content: space-between;
  max-width: 1180px;
}

section.crew .fw-700 {
  opacity: .25;
  margin-right: 10px;
}

section.crew .container__crewinfo_image {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

section.crew .container__crewinfo {
  flex-direction: column;
  justify-content: space-evenly;
  max-height: 600px;
  display: flex;
}

section.crew .container__crewinfo .teammate-infos {
  height: 300px;
  position: relative;
  top: -10px;
}

section.crew .container__crewinfo .selector circle {
  cursor: pointer;
}

section.crew .container__crewinfo .selector circle:hover {
  fill: #fff;
  opacity: .5;
}

section.crew .container__crewinfo h4 {
  color: #fff;
  text-transform: uppercase;
  opacity: .5042;
  margin-bottom: 10px;
  font-family: Bellefair;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

section.crew .container__crewinfo h3 {
  margin-bottom: 20px;
}

section.crew .container__crewinfo p {
  color: #d0d6f9;
  width: 444px;
  font-family: Barlow;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

section.crew .container__crewimage {
  height: 100%;
}

section.crew .container__crewimage .hidden-line {
  display: none;
}

section.crew .container__crewimage .image__teammate {
  background: url("image-douglas-hurley.8e954575.webp") bottom / contain no-repeat;
  flex-shrink: 0;
  width: 568.072px;
  height: 712px;
}

section.crew .container__crewimage .Mark-Shuttleworth {
  background: url("image-mark-shuttleworth.8f632e57.webp") bottom / contain no-repeat;
  width: 453.44px;
  height: 654px;
}

section.crew .container__crewimage .Victor-Glover {
  background: url("image-victor-glover.2b4d3f4f.webp") 50% 50px / contain no-repeat;
  width: 554.388px;
  height: 681px;
}

section.crew .container__crewimage .Anousheh-Ansari {
  background: url("image-anousheh-ansari.63c82a40.webp") 50% 0 / contain no-repeat;
  width: 615.567px;
  height: 650px;
}

section.destination {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

section.destination .fw-700 {
  opacity: .25;
  margin-right: 28px;
}

section.destination .container__image_info {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

section.destination .container__image {
  width: 500px;
  height: 500px;
  position: relative;
}

section.destination .image__planet {
  background: url("image-moon.fe642dbe.webp") center / contain no-repeat;
  align-self: center;
  width: 85%;
  height: 85%;
  position: absolute;
  top: 100px;
  right: 0;
}

section.destination .Mars {
  background: url("image-mars.a8531886.webp") center / contain no-repeat;
}

section.destination .Europa {
  background: url("image-europa.0a58abfb.webp") center / contain no-repeat;
}

section.destination .Titan {
  background: url("image-titan.1e73373c.webp") center / contain no-repeat;
}

.container {
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
  padding: 50px 50px 0;
  display: flex;
}

.container__content {
  border: 1px solid #000;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 24px;
  width: 450px;
  padding-right: 5px;
  display: inline-flex;
}

.container__content p.home-p {
  color: #d0d6f9;
  font-family: Barlow;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

.container__btn {
  justify-content: center;
  height: 274px;
  margin-top: 100px;
  display: flex;
  position: relative;
}

.container__btn .round-btn {
  background: #fff;
  border-radius: 274px;
  flex-shrink: 0;
  align-self: flex-end;
  width: 274px;
  height: 274px;
}

.container__btn .round-btn:hover:before {
  content: "";
  z-index: -1;
  background: #ffffff1a;
  border-radius: 274px;
  width: 450px;
  height: 450px;
  position: absolute;
  top: calc(50% - 225px);
  left: calc(50% - 225px);
}

.container__btn .round-btn a {
  color: #0b0d17;
  letter-spacing: 2px;
  text-transform: uppercase;
  border-radius: 274px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-family: Bellefair;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
  display: flex;
}

.container__informations {
  flex-direction: column;
  justify-content: space-around;
  width: 444px;
  height: 472px;
  margin-top: 90px;
  display: flex;
}

.container__informations .line {
  background: #383b4b;
  flex-shrink: 0;
  width: 444px;
  height: 1px;
  margin-top: 20px;
}

.container__informations nav {
  position: relative;
}

.container__informations nav ul {
  flex-shrink: 0;
  justify-content: space-between;
  width: 285.5px;
  height: 34px;
  display: flex;
}

.container__informations nav ul li {
  color: #d0d6f9;
  letter-spacing: 2.7px;
  cursor: pointer;
  border-bottom: 3px solid #0000;
  font-family: Barlow Condensed;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  list-style: none;
}

.container__informations nav ul li:hover {
  border-bottom: 3px outset #ffffff80;
}

.container__informations nav ul .active {
  color: #fff;
}

.container__informations nav .mini-tabs-highlighter {
  background: #fff;
  width: 39px;
  height: 3px;
  transition: all .3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container__informations p {
  color: #d0d6f9;
  font-family: Barlow;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

.container__informations .informations__travel {
  display: flex;
}

.container__informations .informations__travel .informations__travel__item {
  width: 222px;
}

.container__informations .informations__travel .informations__travel__item p {
  letter-spacing: 2.362px;
  text-transform: uppercase;
  font-family: Barlow Condensed;
  font-size: 14px;
}

.container__informations .informations__travel .informations__travel__item h3 {
  color: #fff;
  text-transform: uppercase;
  font-family: Bellefair;
  font-size: 28px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

